import React from 'react';
import { Table } from '@wix/patterns';
import { TableConfig } from '../../types';
import { useTableFeatures } from '../../hooks';

export interface AutoPatternsTableContentProps {
  configuration: TableConfig;
}

export const AutoPatternsTable = ({
  configuration,
}: AutoPatternsTableContentProps) => {
  const {
    state,
    columns,
    customColumns,
    search,
    title,
    emptyState,
    filters,
    actionCell,
    bulkActionToolbar,
    onRowClick,
    stickyColumns,
    stickySelectionColumn,
  } = useTableFeatures(configuration);

  return (
    <Table
      dataHook="auto-patterns-table"
      search={search}
      title={title}
      state={state}
      columns={columns}
      customColumns={customColumns}
      actionCell={actionCell}
      filters={filters}
      emptyState={emptyState}
      bulkActionToolbar={bulkActionToolbar}
      onRowClick={onRowClick}
      horizontalScroll
      stickyColumns={stickyColumns}
      stickySelectionColumn={stickySelectionColumn}
    />
  );
};
